}

.alert {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: absolute;
  top: 80%;
  left: 40%;
  padding: 10px;
  border-radius: 12px;
  background-color: #F2F3EB;
  -webkit-animation: alertValide 1s backwards;
          animation: alertValide 1s backwards;
  display: none;
  box-shadow: 5px 5px 15px 5px #000000;
}

.logoAlert {
  width: 25px;
  color: green;
}

.logoAlert > svg {
  height: 1.5em;
  width: 1.5em;
}

.msgAlert {
  font-family: 'Noto';
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 0.5px;
  text-align: center;
}

.activeAlert {
  display: flex !important;
}

@-webkit-keyframes alertValide {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 0.5;
  }
  100% {
      opacity: 1;
  }
}

@keyframes alertValide {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 0.5;
  }
  100% {
